        <h2 class="about-h2">About the project</h2>
        <p class="about-p">by Gareth Baldrica-Franklin</p>
        <p class="about-p">Few cartographic inventions are as iconic as the globe. Throughout history, globes have reflected how people conceive of the earth and the heavens—they have been used as status symbols, icons of professionalism, or teaching tools. Print globes are unique in the world of maps and cartography because they exist in three-dimensions, reflecting processes of mapmaking, printing, and construction (Sumira 2014). Scholarship on globes often focuses on specific globe products from Renaissance print shops in Europe, emphasizing great masters like Vincenzo Coronelli. In early 19th and 20th century America, globes were used heavily in education, and thus schoolteachers were involved in the invention of new innovations and processes, many of whom were women (van den Hoonard 2013). American globemakers from this time thus widen the definition of who is considered a cartographer, and their ideas reflect the perspective of regular users of maps and globes (Monmonier 2017) as opposed to a specifically trained class of experts. While some globes from the time survive, I followed Monmonier (2017) into the U.S Patent Archive to examine how globemakers from 1840 to 1940 experimented with globe construction and interaction. </p>
        <p class="about-p">In October 2022, I collected patents dating from 1840 to 1940, utilizing the Public Patent Search on the United States Patent and Trademark Office website. I chose the specific hundred-year period from 1840 to 1940 for two reasons: first, patent records prior to 1836 are incomplete due to a major fire at the patent archive, and secondly, the character of globe production and patents changed considerably after World War II—resulting in more corporate and fewer individual globe patents (Monmonier 2017). I collected patents with a Cooperative Patent Classification (CPC) number of G09B27/008, described as “Terrestrial Globes”. Many patents, especially between 1900-1940, were registered by recognizable faces in the American cartography and globemaking world, such as Rand McNally and Luther Replogle (coincidentally, these companies are two of the only remaining American globe producers today). I excluded these patents, alongside others with familiar names, as these were more likely to have seen commercial production. Additionally, I excluded patents for simple stand apparatuses, prioritizing novel designs, educational attachments, and general absurdities. I also collected patents made of strange, non-traditional globemaking materials, such as rubber. My collection is not complete, and my collection process was not fully systematic, instead designed to highlight inventions that were probably never made.</p>
        <p class="about-p">The archive shows that non-commercial globemakers from 1840-1940 were experimenting with cartographic interaction, and their inventors represent a more diverse population than those often considered in the cartographic and globemaking canon. In the words of Monmonier (2017), “In their exceptionally broad diversity, patented globes might be the epitome of unknown, unacknowledged cartographic innovation.” (p. 210) Globes that maybe were is not the final result, but the first step in the process of exploration. </p>
        <p class="about-p"><b>Sources</b></p>
        <p class="about-p">Monmonier, M. (2017). "Global Affairs." In M. Monmonier, <i>Patents and Cartographic Inventions</i> (pp. 179–217). Springer International Publishing.</p>
        <p class="about-p">Sumira, S. (2014). <i>Globes: 400 Years of Exploration, Navigation, and Power.</i> University of Chicago Press.</p>
