<template>
    <NavBar></NavBar>
    <div class="grid" v-if="globes" >
      <!-- columns -->
      <div class="grid-col grid-col--1"></div>
      <div class="grid-col grid-col--2"></div>
      <div class="grid-col grid-col--3"></div>
      <div class="grid-col grid-col--4"></div>
      <div class="grid-col grid-col--5"></div>
      <div class="grid-col grid-col--6"></div>
      <div v-if="globes" v-for="globe in globes.globes" v-bind:key="globe.id" class="grid-item">
        <router-link class="grid-link" :to="'globe_' + globe.id">
          <img v-bind:src="require('@/assets/thumb/'+ globe.image)" class="globe-img">  
          <div class="globe-info">
            <p class="globe-name">{{globe.name}}</p>
            <p class="globe-author">{{globe.author}}</p>
            <p class="globe-date">{{globe.date}}</p>
          </div>
        </router-link>
      </div>
    </div>
    <!--<div v-if="globes" id="globe-container" class="container-fluid">
        <div class="row">
          <div v-for="globe in globes.globes" v-bind:key="globe.id" class="col-xl-2 col-md-3 col-sm-6 col-12 globe-card">
            <img v-bind:src="require('@/assets/globes/'+ globe.image)" class="globe-img">  
