          </div>
        </div>
    </div>-->
</template>
  
<script>
  import NavBar from "../components/NavBar.vue"
  import Colcade from "colcade/colcade.js"

  export default {
    name: 'GlobesPage',
    components: {
        NavBar
    },
    data(){
    return{
        globes:null
      }
    },
    created(){
      this.fetchData();
    },    
    watch:{
        '$route':'fetchData'
    },
    methods:{
      fetchData(){
        let self = this;  

        var fileName = "./globes.json";
        const myRequest = new Request(fileName);

        fetch(myRequest)
            .then(res => res.json())
            .then(data => {
                self.globes = data;
              })
            .then(d => {
                var grid = document.querySelector('.grid');
                var colc = new Colcade( grid, {
                  columns: '.grid-col',
                  items: '.grid-item'
                });
              }
            )
            .catch(function(error) {
              console.log('Request failed', error)
            });
      }
    }
  }
</script>

